const initialState = {
    kg: null,
    quantity: null,
    value: null
  }
  
  export function plasticReducer(state = initialState, action) {
      switch(action.type) {
        case "SET_KG":
              return {...state, kg: action.payload}
        case "SET_QUANTITY":
                return {...state, quantity: action.payload}
        case "SET_NUMBER":
                 return {...state, value: action.payload}        
      
    default:
    return state
  }
}