import React from "react";
import "./food.css"
import water from "./water_.jpg"
import Select from 'react-select';

let options = require("../Water/dataFood.json");

const arr = [
  { value: '1-2 раз в неделю', label: '1-2 раз в неделю', times: 2 },
  { value: '3-5 раз в неделю', label: '3-5 раз в неделю', times: 4  },
  { value: 'Каждый день', label: 'Каждый день', times: 7},
  { value: 'Несколько раз в день', label: 'Несколько раз в день', times: 18  }
]


 export default class Water extends React.Component  {

    constructor(props) {
        super(props);
        this.state = {
          selectedFood: 1,
          value: "Помидоры",
          imageSrc: "https://curso-de-ruso.com/wp-content/uploads/2019/10/1.png",
          water: null,
          times: ""
         }
      }
      
      onFoodSelected = (food) => {
        this.setState( {
          selectedFood: food.id,
          imageSrc: food.pic,
          water: food.water,
          field: food.field
         
        })
      }
      
      countWater = (time) => {
        this.setState({times: time.times }, function() {
          console.log()
        })
      }

    

    render() {
        return( 
  
            <div className="container">
              <h1>Узнай, сколько воды ты расходуешь</h1>
              <h2>Что ты ешь и как часто?</h2>
                <div className="select">
               <Select options={options} onChange={this.onFoodSelected} key={options.id} placeholder="Выбери продукт" />
               <Select options={arr} onChange={this.countWater} placeholder="Как часто?"/> </div>
        
        
        
        
                <div className="food">
                        <div className="product"> <img src={this.state.imageSrc}/></div>
                <div className="water">
                    <img src={water}/>
                    <p>{this.state.water * this.state.times} л воды в год </p>
                </div>
                </div>
        
            </div>)
    }
}