import React from "react";

import "./Info.css"
import Menu from "../Menu/Menu"
import hero from "../../img/hero.jpg"
import {Animated} from "react-animated-css";

export default class NoPlastic extends React.Component {
render() {
    return(
        <div>
        <Animated animationIn="fadeIn" animationOut="fadeOut"  animationInDuration={1000} isVisible={true}>
      <Menu />
        <div className="noPlastic">
            <h1>Ты - наш герой!</h1>
           
       <img src={hero} />
       <button className="link" onClick={this.handleState}> <a href='/no-plastic-products'>Узнай, как помочь ещё</a></button>
       
    
        </div>
        </Animated>
        </div>
    )
}
   
}

