import React from "react";


import {Animated} from "react-animated-css";

import "../NoPlastic.css"

import coffee1 from "../../../img/coffee1.jpg"
import coffee0 from "../../../img/coffee0.jpg"


class Coffee extends React.Component {

  state = {
    clicked: false
  }
   
changePic = (e) => {
e.target.src = coffee1;
this.setState({clicked: true}, () => console.log("clicked"))
}

 render(){

  return(
    <div className="plastic_wrapper">
       <div className="plastic_item">
       <Animated animationIn="fadeIn" animationOut="fadeOut"  animationInDuration={1000} animationInDelay={500} isVisible={true}>
          <img onClick={this.changePic} className="pic" id="3" src={coffee0}/>
      </Animated>
        {this.state.clicked ? <p>Идеальным решением проблемы стал бы полный отказ от использования одноразовых стаканчиков.</p> :
        <p>Чтобы бумага не протекала, производители покрывают внутренние, а иногда и внешние стенки стаканчика специальной полипропиленовой пленкой.
        Из-за этого такую тару невозможно сдать на переработку.</p>
        }
          
       </div>
       {this.state.clicked ? 
       <div className="arrow">
          <a href="/package"><i className="fa fa-arrow-right"></i> </a>
       </div>
       : null }
    </div>
    )
  }
}

export default Coffee;