import React from "react";

import "./Main.css"
import {Animated} from "react-animated-css";
 

import { Link } from "react-router-dom";



 const Main = () => {
    

    return(
        <Animated animationIn="fadeIn" animationOut="fadeOut"  animationInDuration={1000} isVisible={true}>
        <div className="main_page">
        <div className="title"> <h1>Оцени свой вклад в экологию</h1></div>  
        
         <Link className="list-item"
         to='/calculator' className="eco-calculator">Эко-калькулятор</Link>
        </div>
        </Animated>
    )

}

export default Main;

  