export function setKg(kg) {
    return {
      type: 'SET_KG',
      payload: kg,
    }
  }

  export function setQuantity(resultNumber) {
    return {
      type: 'SET_QUANTITY',
      payload: resultNumber,
    }
  }

  export function setNumber(value) {
    return {
      type: 'SET_NUMBER',
      payload: value,
    }
  }