import React from "react";
import { Link  } from 'react-router-dom';

import "./PlasticYear.css"

import bottle1 from "../../img/bottle1.png"
import strelka from "../../img/strelka.png"
import question from "../../img/question.svg"
import sweater from "../../img/sweater.jpg"
import tshirt from "../../img/tshirt.jpg"
import jacket from "../../img/jacket.jpg"
import carpet from "../../img/carpet.jpg"
import bag from "../../img/bag.jpg"

import Info from "../Info/Info"
import NoPlastic from "../Info/NoPlastic"
import Menu from "../Menu/Menu"
import Recycle from "../Recycle/Recycle"
import Main from "../Main/Main"
import {Animated} from "react-animated-css";

import trash from "../../img/trash.jpg"



class PlasticYear extends React.Component {

  state = {
    value: "",
    quantity: "",
    kg: ""
  }

  
 
  onChangeHandler = (e) => {
    let value = e.target.value;
    let resultNumber = value * 52;
    let resultKg = Math.round(resultNumber * 0.04);
    this.setState({value: value, kg: resultKg, quantity: resultNumber});
    console.log(this.state)

  }

  handleState = () => {
    console.log("Hi")
    this.props.setNumber(this.state.value);
    this.props.setKg(this.state.kg);
    this.props.setQuantity(this.state.quantity);
    console.log(this.props)
   
  }

  
  
    render() {

    
      const { kg, quantity, value } = this.state;
     
console.log(this.state)
            return(
            <div className="plastic_year_wrapper">
            <Menu />
            <Animated animationIn="fadeIn" animationOut="fadeOut"  animationInDuration={1000} isVisible={true}>
            <div className="component">
            <h1>Я использую <input onChange={this.onChangeHandler} value={value}/> бутылок в неделю</h1>  
            
            {this.state.quantity ? 
            <div>
           <div className="info">

            <div><p>или {quantity} бутылок в год</p>
           <img src={bottle1} /></div>

           <div><p>в сумме это {kg} килограммов пластика</p>
           <img src={trash} /></div>

          
           </div> 
           
           
           </div> : null
           
            }

            



          
           
      </div>
      </Animated>
{this.state.quantity ?  <div className="plactic_page">
      <button className="link" onClick={this.handleState}> <Link to='/recycle'>Узнай, что можно произвести из {kg} кг пластика</Link></button>
      </div> : null}

     
     </div>
        
                )
            }

    }

  
export default PlasticYear;