import React from "react";
import { Transition } from 'react-transition-group';
import "../Plastic/PlasticYear.css"
import {Animated} from "react-animated-css";
import Menu from "../Menu/Menu"
import adidas from "../../img/adidas.jpg"
import adidas1 from "../../img/adidas1.jpg"

import { Link } from "react-router-dom";



 const Companies = () => {

 
    return(
        
      <div className="companies">
            <Menu />
            <Animated animationIn="fadeIn" animationOut="fadeOut"  animationInDuration={1000} isVisible={true}>
            <div className="component">
         
            <h5>Adidas</h5>
         <div className="company">
         <div className="company_text">
        <p> Изготавливает кроссовки из волокна, которое создается из переработанного пластика, собранного на пляжах.</p>
        </div>
        <div className="pictures">
        <img src={adidas}></img>
        <img src={adidas1}></img>
        </div>
        <div className="statistics">
            <div className="round">
        <p> На каждую пару уходит в среднем 11 бутылок. </p>
</div>
        </div>
          </div>
        

     </div>
     <div className="arrow">
        <a href="/hm"><i className="fa fa-arrow-right"></i> </a>
        </div>
     </Animated>
     </div>
    )

}

export default Companies;

  