import React from "react";


import {Animated} from "react-animated-css";

import "../NoPlastic.css"

import bottle1 from "../../../img/bottle1_.jpg"
import bottle0 from "../../../img/bottle0_.jpg"


class Bottle extends React.Component {

  state = {
    clicked: false
  }
   
changePic = (e) => {
e.target.src = bottle1;
this.setState({clicked: true}, () => console.log("clicked"))
}

 render(){

  return(
    <div className="plastic_wrapper">
       <div className="plastic_item">
       <Animated animationIn="fadeIn" animationOut="fadeOut"  animationInDuration={1000} animationInDelay={500} isVisible={true}>
          <img onClick={this.changePic} className="pic" id="3" src={bottle0}/>
        </Animated>
          {this.state.clicked ? 
         <div> <p>Используя свою бутылку, ты спасаешь планету от 12 кг пластика в год</p></div>
          : <div> 
          <p>Пластиковые бутылки составляют 40% всех пластиковых отходов. </p>
            <p>Нажми на картинку, чтобы узнать, что можно с этим сделать.</p>
            
            </div>
            }
       </div>
       {this.state.clicked ? 
       <div className="arrow">
          <a href="/coffee"><i className="fa fa-arrow-right"></i> </a>
       </div>
       : null }
    </div>
    )
  }
}

export default Bottle;