import React from "react";
import "./plastic.css"

import bottle from "../../img/bottle.png"
import strelka from "../../img/strelka.png"
import question from "../../img/question.svg"
import sweater from "../../img/sweater.jpg"
import tshirt from "../../img/tshirt.jpg"
import jacket from "../../img/jacket.jpg"
import carpet from "../../img/carpet.jpg"
import bag from "../../img/bag.jpg"


 export default class Plastic extends React.Component  {

    constructor(props){
        super(props);
        this.state = {
            quantity_input: "",
            result: question,
            resultNumber: null
        }
    }

    changeQuantity = (e) => {

        

        this.setState( {
            quantity_input: e.target.value
         }, 
         
         
         
        () => {

          let val = this.state.quantity_input;
          let resultNumber = Math.round(val / 10);

         if(val >= 63 && val<114) {

            this.setState({ result: sweater, resultNumber: resultNumber })  
        } else if (val>=10 && val<13) {
            this.setState( {result: tshirt} )
        } else if (val>=14 && val<19) {
            this.setState ( {result: jacket } )
        } else if (val>=19 && val<63 ) {
            this.setState ( { result: carpet } )
        } else if (val>=114) {
            this.setState ( {result: bag} )
        }
    
    
    }
        )
    }

  

    render(){
        return( 
        <div className="plastic">
        <h2>Укажи количество бутылок, которые ты хочешь сдать в переработку</h2>
        <input type="text" size="3" onChange={this.changeQuantity} />
        <img src={bottle} />
        <img className="strelka" src={strelka} />
        
        <img src={this.state.result} />   
        </div>
        )
    }
}