import React from "react";


import "./Recycle.css"
import {Animated} from "react-animated-css";

import sweater from "../../img/sweater.png"
import tshirt from "../../img/tshirt.png"
import jacket from "../../img/jacket.png"
import Menu from "../Menu/Menu"

import { Link  } from 'react-router-dom';
        
export class Recycle extends React.Component {
    render() {
      const { kg, quantity } = this.props;
        return(
        <div>
          <Menu />
          <Animated animationIn="fadeIn" animationOut="fadeOut"  animationInDuration={1000} isVisible={true}>
        
        <div className="recycle_page">
          
          <div className="componentR">
       {this.props.quantity!=null ? 
        <div>
        <h2 className="produce">Что можно произвести из {kg} кг пластика?</h2>
         <div className="recycled">
       <div><p>{Math.floor(quantity/10)} футболок</p> 
       <Animated animationIn="zoomIn" animationInDuration={2000} isVisible={true}>
       <img src={tshirt} />
       </Animated>
       </div>
       <div> <p className="or">или</p> </div>
       {quantity>=64 ? <div><p>{Math.floor(quantity/63)} свитеров </p> 
       <Animated animationIn="zoomIn" animationInDuration={2000} isVisible={true}>
       <img src={sweater} />
       </Animated>
       
       </div> : <div><p>Почти один свитер</p> <img src={sweater} /></div>}
       <div> <p className="or">или</p> </div>
       <div><p>{Math.floor(quantity/14)} подкладок для куртки </p> 
       <Animated animationIn="zoomIn" animationInDuration={2000} isVisible={true}>
       <img src={jacket} />
       </Animated>
       </div>
       </div>

      
      <button className="link"> <Link to='/no-plastic-products'>Узнай, как можно сократить количество пластика в мире</Link></button>
   
     
       </div>

     
        :
       
       <div> <h2>Произошла ошибка</h2> 
       <p><a href="/plastic">Вернуться назад</a></p>
       </div>
      }
       
      </div>  
 

      
        
      </div> 
      </Animated>

      </div>

      
        )
    }

    }


export default Recycle;
