import React from "react";


import {Animated} from "react-animated-css";

import "../NoPlastic.css"

import napkinks0 from "../../../img/napkins0.jpg"



class Tissues extends React.Component {

  state = {
    clicked: false
  }
   
changePic = (e) => {
e.target.src = napkinks0;
this.setState({clicked: true}, () => console.log("clicked"))
}

 render(){

  return(
    <div className="plastic_wrapper">
    <div className="plastic_item">
    <Animated animationIn="fadeIn" animationOut="fadeOut"  animationInDuration={1000} animationInDelay={500} isVisible={true}>
          <img onClick={this.changePic} className="pic" id="3" src={napkinks0}/> </Animated>
         {this.state.clicked ? <p>Пожалуйста, откажись от использования влажных салфеток</p> :
         <p> Влажные салфетки содержат пластиковые волокна, которые не подвергаются биологическому разложению. </p>}
          
            
       </div>
       {this.state.clicked ? 
       <div className="arrow">
          <a href="/toothbrush"><i className="fa fa-arrow-right"></i> </a>
       </div>
       : null }
    </div>
    )
  }
}

export default Tissues;