import React from "react";
import { Switch, Route, BrowserRouter } from 'react-router-dom';
import DB_CONFIG from "../App/config/config"
import firebase from 'firebase';


import "./Info.css"
import plastic from "../../img/bottles.jpeg"
import weight from "../../img/weight.png"
import bottle1 from "../../img/bottle1.png"
import trash from "../../img/trash.jpg"
import sweater from "../../img/sweater.jpg"
import tshirt from "../../img/tshirt.jpg"



  
  


const products = [ 
    {name: "tshirt", quantity: 10, image: tshirt},
    {name: "sweater", quantity: 20, image: sweater}
]


const Info = (props) => {

    var app = firebase.initializeApp(DB_CONFIG);
    var db = firebase.firestore();

    const getName = () => {
        var docRef = db.collection("plastic").doc("1");
       
        docRef.get().then(function(doc) {
           
               let pis = doc.data();
             return pis.name;
        })
      };
    

   

    return(

        <div>
            <button onClick={getName}>тест</button>
         <p>Во что можно  превратить {props.resultKg} кг пластика? </p>
         <div className="recycled">
           <div>
             <p>{Math.round(props.resultKg/products[0].quantity)} футболок</p>
             
          <img src={products[0].image} />
          </div>
          <p>или</p>
         <div>  <p> {Math.round(props.resultKg/products[1].quantity)} свитеров </p>
         <img src={products[1].image} />
         </div> 
         </div>
         </div>
        
      
            
    )
}


export default Info;