import React from "react";


import {Animated} from "react-animated-css";

import "../NoPlastic.css"

import bag1 from "../../../img/bag1.jpg"
import bag0 from "../../../img/bag0.jpg"


class Package extends React.Component {

  state = {
    clicked: false
  }
   
changePic = (e) => {
e.target.src = bag1;
this.setState({clicked: true}, () => console.log("clicked"))
}

 render(){

  return(
    <div className="plastic_wrapper">
    <div className="plastic_item">
    <Animated animationIn="fadeIn" animationOut="fadeOut"  animationInDuration={1000} animationInDelay={500} isVisible={true}>
          <img onClick={this.changePic} className="pic" id="3" src={bag0}/>
          </Animated>
          {this.state.clicked ? 
          <p>Сумка или "авоська" - отличная альтернатива пластиковым пакетам.</p>:
          <p> При нахождении в земле срок окончательного распада полиэтилена оставляет около 100-400 лет.</p>
               }
       </div>
       {this.state.clicked ? 
       <div className="arrow">
          <a href="/tissues"><i className="fa fa-arrow-right"></i> </a>
       </div>
       : null }
    </div>
    )
  }
}

export default Package;