import React from "react";

import Menu from "../Menu/Menu"
import {Animated} from "react-animated-css";
import Bottle from "../NoPlasticProducts/Bottle/Bottle"

import "./NoPlastic.css"
import bag0 from "../../img/bag0.jpg"
import bag1 from "../../img/bag1.jpg"
import coffee0 from "../../img/coffee0.jpg"
import coffee1 from "../../img/coffee1.jpg"
import napkins0 from "../../img/napkins0.jpg"
import napkins1 from "../../img/napkinks1.jpg"
import bottle1 from "../../img/bottle1_.jpg"
import bottle0 from "../../img/bottle0_.jpg"
import tooth0 from "../../img/tooth0.jpg"
import tooth1 from "../../img/tooth1.jpg"
import plate0 from "../../img/plate0.jpg"
import plate1 from "../../img/plate1.jpg"




class NoPlasticProducts extends React.Component {
     constructor(props) {
         super(props);
     
    this.state = {
        noplastic: [bag1, coffee1, napkins1, bottle1, tooth1, plate1],
        plastic:  [bag0, coffee0, napkins0, bottle0, tooth0, plate0],
        clicked: false
    }
}



onClickHandler = (e) => {
    let id = e.target.id;
e.target.src = this.state.noplastic[id];
this.setState( {clicked: true} )
this.addParagraph();
}

addParagraph = () => {
    let plastic_item = document.querySelectorAll(".plastic_item");
    plastic_item.forEach(p => p.addEventListener("click", function(e) {
        console.log(e)
    }) )
    
}


 render(){

    

        return(
         
    <div className="noPlasticPage">  
    <Menu />
    <Animated animationIn="fadeIn" animationOut="fadeOut" animationInDuration={1000} isVisible={true}>
      <div className="noPlasticWrapper">
       <Bottle />
      </div>
            </Animated>
           
            </div>
           
            
        )

    }

 

}

export default NoPlasticProducts;