import React from "react";


import {Animated} from "react-animated-css";

import "../NoPlastic.css"

import tooth1 from "../../../img/tooth1.jpg"
import tooth0 from "../../../img/tooth0.jpg"


class Toothbrush extends React.Component {

  state = {
    clicked: false
  }
   
changePic = (e) => {
e.target.src = tooth1;
this.setState({clicked: true}, () => console.log("clicked"))
}

 render(){

  return(

  
   
    <div className="plastic_wrapper">
    <div className="plastic_item">
    <Animated animationIn="fadeIn" animationOut="fadeOut"  animationInDuration={1000} animationInDelay={500} isVisible={true}>
          <img onClick={this.changePic} className="pic" id="3" src={tooth0}/> </Animated>
          {this.state.clicked ? <div>
          <p> Бамбуковая щетка полностью разложится в почве за 3 года. </p>
           <p>Растения бамбука растут крайне быстро (более метра в день), занимают меньше площади, 
               нуждаются в небольшом уходе и могут процветать 
               без удобрений и пестицидов.
             
          </p> 
          </div>
          :
          <div>
          <p>Традиционные зубные щетки сделаны из комбинации пластика (сырой нефти) и резины для рукоятки, а также нейлона — для щетины.</p> 
          <p>Ни один из этих материалов не является биоразлагаемым, поэтому их судьба — быть мусором на нашей планете вечно. </p></div>
               }
     
    </div>

    {this.state.clicked ? 
       <div className="arrow">
          <a href="/lastpage"><i className="fa fa-arrow-right"></i> </a>
       </div>
       : null }
    </div>
    
    )
  }
}

export default Toothbrush;