import React from "react";
import "./food.css"
import water from "./water_.jpg"

 export default class Food extends React.Component  {

    render(){
        return( 
        <div className="food">
                <div className="product"> <img src={this.props.image}/></div>
        <div className="water">
            <img src={water}/>
            <p>{this.props.water} л воды в год </p>
        </div>
        </div>
        )
    }
}