import React from "react";
import Menu from "../../Menu/Menu"

import {Animated} from "react-animated-css";
import planet from "../../../img/planet.png"

import "../NoPlastic.css"



class LastPage extends React.Component {

 render(){

  return(
      <div>
          <Menu />
    <div className="last_page">
       <div className="plastic_item">
         
         Спасибо, что бережёшь нашу планету! 
         <Animated animationIn="fadeIn" animationOut="fadeOut"  animationInDuration={2000} animationInDelay={500} isVisible={true}>
         <img src={planet} /> </Animated>
          
       </div>
       </div>
       </div>
      
    )
  }
}

export default LastPage;