import React from 'react';
import { Switch, Route, BrowserRouter} from 'react-router-dom';
import { connect } from "react-redux"
 
import './App.css';
import Water from "../Water/Water"
import Design from "../Design/design"
import LastPage from "../NoPlasticProducts/LastPage/LastPage"
import Companies from "../Companies/Companies"
import Main from "../Main/Main"
import Menu from "../Menu/Menu"
import Plastic from "../Plastic/Plastic"
import PlasticYear from "../Plastic/PlasticYear"
import {DB_CONFIG} from "./config/config"
import Calculator from "../Calculator/Calculator"
import NoPlastic from "../Info/NoPlastic"
import NoPlasticProducts from "../NoPlasticProducts/NoPlasticProducts"
import Recycle from "../Recycle/Recycle";
import Info from "../Info/Info"
import HM from "../Companies/hm"
import Food from "../../Components/Water/Food"
import Coffee from "../NoPlasticProducts/Coffee/Coffee"
import Package from "../NoPlasticProducts/Package/Package"
import Tissues from "../NoPlasticProducts/Tissues/Tissues"
import Toothbrush from "../NoPlasticProducts/Toothbrush/Toothbrush"
import Plates from "../NoPlasticProducts/Plates/Plates"
import { setKg, setQuantity, setNumber } from "../../redux/actions/ResultActions"



import {Animated} from "react-animated-css";

class App extends React.Component {
  render(){
const { result, setKgAction, setQuantity, setNumber } = this.props;

  

    return (

 
<BrowserRouter basename="/eco" >
<Route exact path='/' component={Main} />
<Route exact path='/coffee' component={Coffee} />
<Route exact path='/lastpage' component={LastPage} />
<Route exact path='/tissues' component={Tissues} />
<Route exact path='/package' component={Package} />
<Route exact path='/plates' component={Plates} />
<Route exact path='/toothbrush' component={Toothbrush} />
<Route exact path='/hm' component={HM} />
<Route exact path='/companies' component={Companies} />
<Route exact path='/design' component={Design} />
<Route exact path='/water' component={Water} />
<Route exact path='/info' component={Info} />
<Route exact path='/calculator' component={Calculator}  />
<Route exact path="/plastic"
  component={() => <PlasticYear value={result.value} setNumber={setNumber} kg={result.kg} setKg={setKgAction} quantity={result.quantity} setQuantity={setQuantity} />}
/>
<Route exact path='/noplastic' component={NoPlastic} />
<Route
  exact path="/recycle"
  component={() => <Recycle kg={result.kg} quantity={result.quantity} />}
/>
<Route exact path='/no-plastic-products' component={NoPlasticProducts} />
</BrowserRouter>

    )
}
}


const mapStateToProps = store => {
  console.log(store)
  return {
    user: store.user,
    result: store.result
  }
}

const mapDispatchToProps = dispatch => {
  return {
    
    calculatingKg: kg => dispatch(setKg(kg)),
    setKgAction: resultKg => dispatch(setKg(resultKg)),
    setQuantity: resultNumber => dispatch(setQuantity(resultNumber)),
    setNumber: value => dispatch(setNumber(value)),

  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App)